<template>
  <div>
    <div>
      <img id="logo-img" src="../assets/brand.png" />
    </div>
    <h1 style="text-align:center">{{$t('lang.setting')}}</h1>
  <div class="about-content">
    <!-- <el-row> -->
      <el-link type="primary" href="https://github.com/lithiumice">AUTHOR:lithiumice</el-link>
      <br />
      <el-link type="success" href="https://github.com/lithiumice">GITHUB:https://github.com/lithiumice</el-link>
      <br />
    <!-- </el-row> -->
    <div class="choose-locale">
      <a>language:</a>
      <el-select v-model="locale" placeholder="please select">
        <el-option
          v-for="item in localeOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      locale: "zh-CN",
      localeOptions: [
        {
          value: "zh-CN",
          label: "中文",
        },
        {
          value: "en-US",
          label: "english",
        },
      ],
    };
  },
  watch: {
    locale(newValue, oldValue) {
      this.$i18n.locale = newValue;
    },
  },
};
</script>

<style scoped>
#logo-img {
  width: 180px;
  height: 220px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.about-content {
  /* width: 30em; */
  text-align: center;
  margin: 20px;
}
.choose-locale span {
  float: left;
}
.choose-locale el-select {
  margin-left: 90px;
}
.el-row{
  margin-bottom: 20px;
}
</style>


