<template>
  <div id="app">
    <el-row :gutter="0">
      <el-col :span="12">
        <div class="left-card no-drag">
          <router-view />
        </div>
      </el-col>
      <el-col :span="12">
        <el-card class="el-card no-drag">
          <lottie
          v-show="lottieType==0"
            :options="emailLottieOptions"
            :height="250"
            :width="250"
            v-on:animCreated="handleAnimation"
          />
          <lottie
          v-show="lottieType==1"
            :options="workingLottieOptions"
            :height="250"
            :width="250"
            v-on:animCreated="handleAnimation"
          />
          <div id="hint-text" v-text="hintText"></div>
        </el-card>
        <el-card v-if="showSelectBtn" class="el-card no-drag">
          <div id="button-group">
            <el-button>{{$t('lang.metal')}}</el-button>
            <el-button>{{$t('lang.plastic')}}</el-button>
            <el-button>{{$t('lang.paper')}}</el-button>
            <el-button>{{$t('lang.others')}}</el-button>
          </div>
        </el-card>
        <el-card class="el-card no-drag">
          <div id="sys-button">
            <el-button @click="sendFetchSingal">{{$t('lang.refresh')}}</el-button>
            <el-button @click="closeWindow">{{$t('lang.close')}}</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Lottie from "vue-lottie";
import * as animationDataEmail from "./assets/email.json";
import * as animationDataWorking from "./assets/working.json";

export default {
  name: "app",
  components: {
    lottie: Lottie,
  },
  data() {
    return {
      showSelectBtn:false,
      animationSpeed: 0.4,
      // lottieType:0,
      emailLottieOptions:{animationData: animationDataEmail },
      workingLottieOptions:{animationData: animationDataWorking}
    };
  },
  methods: {
    handleAnimation: function (anim) {
      this.anim = anim;
    },
    onSpeedChange: function () {
      this.anim.setSpeed(this.animationSpeed);
    },
    sendFetchSingal() {
      this.$socket.emit("fetchData");
    },
    closeWindow(){
      window.close()
    },
    toggle(){
      this.defaultOptions={animationData: animationDataWorking}
    }
  },
  computed: {
    hintText(){
       return this.$store.state.hintText || this.$i18n.t('lang.hintTextWait')
    },
    lottieType(){
      return this.$store.state.hintType
    }
    // defaultOptions(){
    //   return (this.$store.state.hintType==0) ? {animationData: animationDataEmail } : {animationData: animationDataWorking }
    // }
  },
  mounted() {
  },
};
</script>
<style scoped>
#hint-text {
  width: 100%;
  text-align: center;
  font-size: 25px;
  margin-bottom: 40px;
}
#app {
  background-color: gray;
}
.left-card {
  height: 100%;
  margin: 20px;
}
.el-card {
  margin: 20px;
}
</style>


