import Vuex from 'vuex'
import Vue from 'vue'


Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        leftSpace: {
            'metal': 15,
            'plastic': 15,
            'paper': 15,
            'others' : 15
        },
        hintText: '',
        hintType:0,
    },
    mutations: {
        SOCKET_leftSpaceUpdate(state, data) {
            state.leftSpace = data
            console.log(data)
        },
        SOCKET_hintTextUpdate(state, data) {
            state.hintText=data.hintText
            state.hintType=data.hintType
            console.log(data)
        },
    }
})


