<template>
  <el-container class="root">
    <el-header>
      <a class="head-title">
        {{$t('lang.dashboardTitle')}}
      </a>
      </el-header>
    <el-main>
    <el-card class="card">
        <span class="progress-label">{{$t('lang.metal')}}:</span>
      <el-progress class="progress-bar" :text-inside="true" :stroke-width="26" :percentage="metalPercentage" :color="colors"></el-progress>
    </el-card>
    <el-card class="card">
      <span class="progress-label">{{$t('lang.plastic')}}:</span>
      <el-progress class="progress-bar" :text-inside="true" :stroke-width="26" :percentage="plasticPercentage" :color="colors"></el-progress>
    </el-card>
    <el-card class="card">
      <span class="progress-label">{{$t('lang.paper')}}:</span>
      <el-progress class="progress-bar" :text-inside="true" :stroke-width="26" :percentage="paperPercentage" :color="colors"></el-progress>
    </el-card>
    <el-card class="card">
      <span class="progress-label">{{$t('lang.others')}}:</span>
      <el-progress class="progress-bar" :text-inside="true" :stroke-width="26" :percentage="othersPercentage" :color="colors"></el-progress>
    </el-card>
    </el-main>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      colors: [
        { color: "#f56c6c", percentage: 20 },
        { color: "#e6a23c", percentage: 40 },
        { color: "#5cb87a", percentage: 60 },
        { color: "#1989fa", percentage: 80 },
        { color: "#6f7ad3", percentage: 100 },
      ],
    };
  },
  computed: {
      metalPercentage(){
        return this.$store.state.leftSpace['metal']
      },
      plasticPercentage(){
        return this.$store.state.leftSpace['plastic']
      },
      paperPercentage(){
        return this.$store.state.leftSpace['paper']
      },
      othersPercentage(){
        return this.$store.state.leftSpace['others']
      },
  },
};
</script>
<style scoped>
.root {
  height: 100%;
  width: 100%;
  text-align: center;
}
.card {
  margin: 10px;
}
.progress-label{
  float: left;
}
.progress-bar{
  margin-left: 60px;
}
.head-title {
  margin: auto auto auto 0;
  font-size: 30px;
}
.el-header {
  background-color: purple;
  color: white;
  height: auto;
  width: 100%;
}
</style>


